<template>
    <div class="yingyong">
        <headerBar :title="headerNavData"></headerBar>
        <div class="benfen">
           <h1>确认你的助记词</h1>
           <p>根据前面从左向右填写，重新按顺序填写</p>
        </div>
        <div class="notice">
          <yd-textarea slot="right"  v-model="content" placeholder=""></yd-textarea>
          <p class="zjcct">
            <span @click="addzjc(item,index)" v-for="(item,index) in zjcbtn" :class="{'bj':item.isclass}">{{item.name}}</span>
          </p>
          <button type="button" @click="gobeifenkey()">确认</button>
        </div>
    </div>
  
</template>

<script>
  import headerBar from './header.vue'
export default {
  components:{
      "headerBar":headerBar,
  },
  data () {
    return {
      headerNavData:{
         left:true,
         title:'填写助记词',
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      },
      zjcbtn:[
        {name:'sdf',isclass:false,isadd:false},
        {name:'fgh',isclass:false,isadd:false},
        {name:'rty',isclass:false,isadd:false}
      ],
      content:''
    }
  },
  methods:{
    addzjc(obj,index){
      var content = this.content;
      if (obj.isadd) {
        this.content = content.split(obj.name+"  ")[0] + content.split(obj.name+"  ")[1]
        obj.isclass = false
        obj.isadd = false
      }else{
        this.content = content + obj.name+"  "
        obj.isclass = true
        obj.isadd = true
      }
    },
    gobeifenkey(){
      this.$router.push({path:'/beifen_key'})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

.yingyong{
  padding-top: .9rem
}
.benfen{
  padding: .5rem 0 .35rem;
  text-align: center;
}
.benfen h1{
  text-align: center;
  line-height: 1.5;
  font-size: .3rem;
  color: #278eed;
  margin-bottom: .35rem;
}
.benfen p{
  text-align: center;
  line-height: 1.5;
  font-size: .28rem;
  color: #333;
}
.notice{
  padding: 0rem .2rem;
}
.notice h1{
  font-weight: 100;
  font-size: .3rem;
  color: #c20919;
  margin-bottom: .2rem;
}
.notice p{
  font-size: .28rem;
  color: #333;
  line-height: 1.5;
  margin-bottom: .1rem
}
.notice button{
  background: #278eed;
  height: .7rem;
  line-height: .7rem;
  width: 100%;
  text-align: center;
  color: #fff;
  border:none;
  font-size: .3rem;
  border-radius: 5px;
  margin: .4rem 0
}
.notice button:active{
  background: #3785cd;
}
.yd-textarea{
  border: 1px solid #ddd;
  padding: .3rem;
  color: #333;
  margin: .5rem 0;
}
.zjcct{
  padding: .25rem;
}
.zjcct span{
  padding: .05rem .2rem;
  display: inline-block;
  border-radius: 4px;
  margin: .12rem
}
.bj{
  color: #fff;
  background: #278eed;
}
</style>



